body {
    font-family: Arial, sans-serif;
    background-color: #141414;
    color: #fff;
    text-align: center;
    margin: 0;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h1 {
    color: #fff;
    font-size: 3rem;
}

button {
    background-color: #007bff;
    font-size: 1.25rem;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
    transition: .2s;
    &:hover {
        background-color: #0056b3;
    }
}

p {
    margin-top: 20px;
    font-size: 18px;
    color: #555;
}